import React from 'react';
import { Cloudinary } from '@cloudinary/url-gen';
import './App.css';
import NavigationBar from "./Components/NavigationBar"
import ScrollBar from "./Components/ScrollBar"
import InformationContainer from './Components/InformationContainer';
import LargeInformationContainer from './Components/LargeInformationContainer';
import FooterComponent from './Components/FooterComponent';
import SmallInformationContainer from './Components/SmallInformationContainer';

function App() {

  const cld = new Cloudinary({
    cloud: {
      cloudName: 'dzifbqu3y'
    }
  });
  
  return (
    <div>
      <NavigationBar/>
      <main>
        <ScrollBar/>
        <div>
          <InformationContainer image = {cld.image("leaf/warning")} title = " " mainText = " ,           .          ,   ."/>
          <InformationContainer image = {cld.image("leaf/whiteBackgroundSalad")} title = "  ?" mainText = "       ,       Leaf    .              ,            ''."/>
        </div>
        <LargeInformationContainer image = {cld.image("leaf/NewJuice")} title = "   Leaf" mainText = "      .         .            .                 .       !" />
        <div>
          <SmallInformationContainer image = {cld.image("leaf/workWithUs")} title = "  Leaf" mainText = "   Leaf.       ,   . " />
          <SmallInformationContainer image = {cld.image("leaf/newSalad")} title = " " mainText = "        ." />
          <SmallInformationContainer image = {cld.image("leaf/GreenEarth")} title = " " mainText = "   ' '      .    !" />
        </div>
      </main>
      <FooterComponent/>
    </div>
  );
}

export default App;