import React, { useState } from 'react'
import Select, { Option } from 'rc-select'
import "./FindUs.css"
import { Cloudinary } from "@cloudinary/url-gen"
import { AdvancedImage } from "@cloudinary/react"
import JSON from "../JSON/FindUs.json"
import NavigationBar from "../Components/NavigationBar"
import Footer from "../Components/FooterComponent"

export default function FindUs() {

    const CityList = JSON.Cities
    const [SelectedCity, NewSelectedCity] = useState("Odesa")
    const CityData = JSON.CityData[SelectedCity]
    const [SelectedRegion, NewSelectedRegion] = useState("")

    const cld = new Cloudinary({
        cloud: {
            cloudName: 'dzifbqu3y'
        }
    });

    return (
        <div className='FindUs'>
            <NavigationBar />
            <AdvancedImage cldImg={cld.image("leaf/FindUs")} />
            <form>
                <div className='custom_select_container'>
                    <div className='custom_select'>{SelectedCity}</div>
                    <ul>{CityList.map(item => <li className='custom_option' onClick={() => NewSelectedCity(item)} key={item} value={item}>{item}</li>)}</ul>
                </div>
            </form>
            <Select>
                <Option value="jack">jack</Option>
                <Option value="lucy">lucy</Option>
                <Option value="yiminghe">yiminghe</Option>
            </Select>
            <Footer />
        </div >
    )
}
